<template>
  <el-container class="home_container">
    <el-header>
      <div>
        <img src="../assets/heima.png" alt />
        <span></span>
      </div>
      <el-button type="info" @click="loginout"></el-button>
    </el-header>
    <!--  -->
    <el-container>
      <!--  -->
      <el-aside :width="isCollapse ? '64px' : '200px'">
          <div class='toggle-button' @click='toggleCollapse'>|||</div>
        <!--  -->
        <el-menu background-color="#333744" text-color="#fff" active-text-color="#409EFF" :default-active='activePath' unique-opened :collapse='isCollapse' :collapse-transition='false' router>
          <!--  -->
          <el-submenu :index="item.id + ''" v-for="item in MenuList" :key ='item.id'>
            <!--  -->
            <template slot="title">
              <!--  -->
              <i :class="iconObj[item.id]"></i>
              <!--  -->
              <span>{{ item.authName }}</span>
            </template>
            <!--  -->
            <el-menu-item :index="'/'+subItem.path" v-for="subItem in item.children" :key="subItem.id" @click="saveState('/'+subItem.path)">
              <template slot="title">
                <!--  -->
                <i class="el-icon-menu"></i>
                <!--  -->
                <span>{{ subItem.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
          <!--  -->
          <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
        //
        MenuList:[],
        iconObj:{
            '125':'iconfont icon-user',
            '103':'iconfont icon-tijikongjian',
            '101':'iconfont icon-shangpin',
            '102':'iconfont icon-danju',
            '145':'iconfont icon-baobiao'
        },
        isCollapse:false,
        //
        activePath:''
    }
  },
  watch: {},
  computed: {},
  methods: {
    //
    loginout() {
      //token
      window.sessionStorage.clear()
      //
      this.$router.push('/login')
    },
    async getMenuList(){
       const { data:res } = await this.$http.get('menus')
       if(res.meta.status !== 200) return this.$message.error(res.meta.msg)
       this.MenuList = res.data
       console.log(res)
    },
    //,
    toggleCollapse(){
      this.isCollapse = !this.isCollapse
    },
    saveState(activePath){
         window.sessionStorage.setItem('activePath',activePath)
         this.activePath = activePath
    }
  },
  created() {
      this.getMenuList()
      this.activePath = window.sessionStorage.getItem('activePath')
  },
  mounted() {},
}
</script>
<style lang="less" scoped>
.home_container {
  height: 100%;
}
.el-header {
  background: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 20px;
    }
  }
}
.el-aside {
  background: #333744;
  .el-menu{
      border-right:none;
  }
}
.el-Main {
  background: #eaedf1;
}
.iconfont {
    margin-right:15px;
}
.toggle-button{
    background: #4a4a4a;
    font-size:10px;
   line-height:24px;
   color:#fff;
   text-align:center;
   letter-spacing: 0.2em;
   cursor: pointer;

}
</style>