<template>
  <div class="order">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" style="width: 100%" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop='order_number' width='200'></el-table-column>
         <el-table-column label="" prop='order_price'></el-table-column>
          <el-table-column label="" prop='pay_status'>
              <template slot-scope="scope">
                 <el-tag type='success' v-if="scope.row.pay_status === '1'"></el-tag>
                 <el-tag type='danger' v-else></el-tag>
              </template>
          </el-table-column>
           <el-table-column label="" prop='is_send'></el-table-column>
            <el-table-column label="" prop='create_time'>
                <template slot-scope="scope">
                    <div>
                        {{ scope.row.create_time }}
                    </div>
                </template>
            </el-table-column>
             <el-table-column label="">
                 <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size='mini' @click='showBox'></el-button>
                    <el-button type="success" icon="el-icon-location" size='mini'></el-button>

                 </template>
             </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15]"
      :page-size="queryInfo.pagenum"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="showAddVisible"
  width="50%"
  >
  <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px" class="demo-ruleForm">
  <el-form-item label="" prop="address1">

   
  </el-form-item>
  <el-form-item label="" prop="address2">
    <el-input v-model="addressForm.address2"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="showAddVisible = false"> </el-button>
    <el-button type="primary" @click="showAddVisible = false"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>

export default {
  components: {},
  props: {},
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
      },
      orderList: [],
      total: 0,
      showAddVisible:false,
      addressForm:{
         address1:[],
         address2:''
      },
      addressFormRules:{
          address1:[
              {
                  required:true,message:'',trigger:'blur'
              }
          ],
          address2:[
              {
                  required:true,message:'',trigger:'blur'
              }
          ]
      },
     
    }
  },
  watch: {},
  computed: {},
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.total = res.data.total
      this.orderList = res.data.goods
    },
    // 
    handleSizeChange(newSize){
        this.queryInfo.pagesize = newSize
        this.getOrderList()
    },
    handleCurrentChange(newPage){
        this.queryInfo.pagenum = newPage
        this.getOrderList()
    },
    //
    showBox(){
       this.showAddVisible = true
    }

  },
  created() {
    this.getOrderList()
  },
  mounted() {},
}
</script>
<style lang="less" scoped>
</style>