<template>
  <div class="fby_login">
    <div class="fby_login_box">
      <!--  -->
      <div class="fby_avatar_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!--  -->
      <el-form ref='Loginref' :model='Loginform' :rules="Loginrules" label-width="0px" class='fby_login_form'>
          <!--  -->
        <el-form-item prop='username'>
          <el-input v-model='Loginform.username' prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop='password'>
          <el-input type='password' v-model='Loginform.password' prefix-icon="iconfont icon-3702mima"></el-input>
        </el-form-item>
        <!-- button -->
        <el-form-item class='fby_btns'>
          <el-button type="primary" @click='Loginvalidate'></el-button>
           <el-button type="info" @click='Loginreset'></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
        //
         Loginform: {
          username: 'admin',
          password: '123456'
        },
        Loginrules:{
            //
            username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
            { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
          ],
        }
    };
  },
  watch: {},
  computed: {},
  methods: {
    //
    Loginreset(){
        this.$refs.Loginref.resetFields()
    },
    //
     Loginvalidate(){
        this.$refs.Loginref.validate(async valid =>{
            if(!valid) return;
            const { data:res } = await this.$http.post('login',this.Loginform)
            if(res.meta.status !== 200)return this.$message.error('')
             this.$message.success('')
             console.log(res)
             //1.tokensessionStorage
            //  1.1API
            // 1.2tokentokensessionStorage
             window.sessionStorage.setItem('token',res.data.token)
             //2./home
             this.$router.push('/home')
        })
    }
  },
  created() {},
  mounted() {},
};
</script>
<style lang="less" scoped>
.fby_login {
  background: #2b4b6b;
  height: 100%;
}
.fby_login_box {
  width: 450px;
  height: 300px;
  background: #fff;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .fby_avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    background: #fff;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: #eee;
    }
  }
}
.fby_login_form{
    position:absolute;
    bottom:0;
    width:100%;
    padding:0 20px;
    box-sizing: border-box;
}
.fby_btns{
    display:flex;
    justify-content: flex-end;
}
</style>