<template>
  <div class="cate">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>

          <el-button type="primary" @click="addCate"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        class="treeTab"
        :data="cateList"
        :columns="columns"
        :expand-type="false"
        index-text="#"
        border
        :show-row-hover="false"
        show-index
        :selection-type="false"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color:lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color:red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click='editCatelog(scope.row.cat_id)'></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click='removeCate(scope.row.cat_id)'></el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addCateVisible" width="50%" @close='addCateClose'>
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="ParentCateList"
            :props="cascaderProps"
            @change="ParentCateChanged"
            clearable
            
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateVisible = false"> </el-button>
        <el-button type="primary" @click="addConfirm"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="editaddCatelog"
  width="50%"
  @close='editCateClose'
  >
  <!--  -->
   <el-form :model="editCate" ref ='editCateRef' label-width="70px"  :rules="editCateFormRules">
  <el-form-item label="" prop='cat_name'>
    <el-input v-model="editCate.cat_name" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editaddCatelog = false"> </el-button>
    <el-button type="primary" @click="editCateInfo"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      //
      cateList: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      editCate:{
        cat_name:''
      },
      //
      total: 0,
      addCateForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0,
      },
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur',
          },
        ],
      },
      editCateFormRules:{
        cat_name:[
           { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur',
          },
        ]
      },
      columns: [
        {
          label: '',
          prop: 'cat_name',
        },
        {
          label: '',
          type: 'template',
          template: 'isok', //
        },
        {
          label: '',
          type: 'template',
          template: 'order', //
        },
        {
          label: '',
          type: 'template',
          template: 'opt', //
        },
      ],
      //   
      addCateVisible: false,
      editaddCatelog:false,
      //
      ParentCateList: [],
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
      },
      //
      selectedKeys: [],
    }
  },
  watch: {},
  computed: {},
  methods: {
    //
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //cateList
      this.cateList = res.data.result
      this.total = res.data.total
    },
    //pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    //pagenum
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    //
    addCate() {
      this.getParentCateList()
      this.addCateVisible = true
    },
    
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 },
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ParentCateList = res.data
      console.log(this.ParentCateList)
    },
    //
    ParentCateChanged() {
      console.log(this.selectedKeys)
      //selectedKeys0
      //
      if(this.selectedKeys.length > 0){
       this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length-1]
       this.addCateForm.cat_level = this.selectedKeys.length
       return
      }else{
        this.addCateForm.cat_pid = 0
       this.addCateForm.cat_level = 0
      }
    },
    addConfirm(){
     this.$refs.addCateFormRef.validate( async valid =>{
       if(!valid) return
         const { data:res } = await this.$http.post('categories',this.addCateForm)
         if(res.meta.status !== 201){
           return this.$message.error('')
         }
         this.$message.success('')
         this.getCateList()
         this.addCateVisible = false
     })
    },
    //
    addCateClose(){
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
       this.addCateForm.cat_pid = 0
       this.addCateForm.cat_level = 0
    },
    //
    editCateClose(){
       this.$refs.editCateRef.resetFields()
    },
    //
    async editCatelog(id){

       this.editaddCatelog = true
       console.log(id)
      const { data:res } = await this.$http.get('categories/'+id)
      console.log(res)
      if(res.meta.status !== 200){
          return this.$message.error('')
      }
      this.editCate = res.data
    },
    //
    editCateInfo(){
     this.$refs.editCateRef.validate( async valid =>{
            console.log(valid)
            if(!valid) return 
            //
           const { data:res } = await this.$http.put('categories/'+this.editCate.cat_id,{
              cat_name:this.editCate.cat_name
              
           })
           if(res.meta.status !== 200){
              return this.$message.error('')
           }
            //
            this.editaddCatelog = false
            this.$message.success('')
             this.getCateList()//
        })
    },
    //
     async removeCate(id){
        //
      const Reault = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        // cancel
          if(Reault !== 'confirm'){
              return this.$message.info('')
          }
          const {data:res } = await this.$http.delete('categories/' + id)
          if(res.meta.status !== 200){
               return this.$message.error('')
          }
          this.$message.success('')
          this.getCateList()
    },
  },
  created() {
    this.getCateList()
  },
  mounted() {},
}
</script>
<style lang="less" scoped>
.treeTab {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;

}
</style>