<template>
  <div class="roles">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click='addRoleDesc=true'></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="roleList" style="width: 100%" border stripe>
          <el-table-column type='expand' label="" width='100'>
              <template slot-scope="scope">
                  <el-row :class="['bdbottom',i1 === 0 ? 'bdtop' : '','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                      <!--  -->
                      <el-col :span="5">
                          <el-tag closable @close='removeRightById(scope.row,item1.id)'>
                              {{ item1.authName }}
                          </el-tag>
                          <i class='el-icon-caret-right'></i>
                      </el-col>
                      <!--  -->
                       <el-col :span="19">
                             <el-row :class="[i2 === 0 ? '' :'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id" >
                                 <el-col :span='6'>
                                     <el-tag type='success' closable @close='removeRightById(scope.row,item2.id)'>{{ item2.authName }}</el-tag>
                                      <i class='el-icon-caret-right'></i>
                                 </el-col>
                                  <el-col :span='18'>
                                      <el-tag type="warning" v-for="(item3,i3) in item2.children" closable :key="item3.id"  @close='removeRightById(scope.row,item3.id)'>
                                               {{ item3.authName }}
                                      </el-tag>
                                  </el-col>
                             </el-row>
                       </el-col>
                  </el-row>
                  <!-- <pre>
                      {{ scope.row }}
                  </pre> -->
              </template>
          </el-table-column>
          <!--  -->
        <el-table-column type="index" label="" width='100'></el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column  label="">
            <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click='editDesclog(scope.row.id)' size='mini'></el-button>
                <el-button type="danger" icon="el-icon-delete"  size='mini' @click='editRemove(scope.row.id)'></el-button>
                <el-button type="warning" icon="el-icon-setting"  size='mini' width='200px' @click='showDesc(scope.row)'></el-button>
            </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
<el-dialog
  title=""
  :visible.sync="editDescdialog"
  width="50%"
  @close='editdescClose'
  >
  <!--  -->
   <el-form :model="editDescs" ref ='editDescRef' label-width="70px" >
  <el-form-item label="">
    <el-input v-model="editDescs.roleName" ></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="editDescs.roleDesc" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDescdialog = false"> </el-button>
    <el-button type="primary" @click="editDescInfo"> </el-button>
  </span>
</el-dialog>
<!--  -->
 <el-dialog
  title=""
  :visible.sync="addRoleDesc"
  width="50%"
  @close='addDescClose'
  >
  <!--  -->
   <el-form :model="addDesc" :rules="addFormRules"  ref = 'addDescRef' label-width="70px" >
  <el-form-item label="">
    <el-input v-model="addDesc.roleName"></el-input>
  </el-form-item>
   <el-form-item label="">
    <el-input v-model="addDesc.roleDesc" ></el-input>
    </el-form-item>
  </el-form>
  <!--  -->
  <span slot="footer" class="dialog-footer">
    <el-button @click="addRoleDesc = false"> </el-button>
    <el-button type="primary" @click="addroleDesc"> </el-button>
  </span>
</el-dialog>

<!--  -->
<!--  -->
     <el-dialog
  title=""
  :visible.sync="showRight"
  width="30%"
  @close='setRightDialogClose'
  >
  <!--  -->
  <el-tree :data="rightList" :props="treeProps" ref="treeRef" show-checkbox node-key='id' default-expand-all :default-checked-keys='defkeys'></el-tree>
  <span slot="footer" class="dialog-footer">
    <el-button @click="showRight = false"> </el-button>
    <el-button type="primary" @click="allotRights"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      //
      roleList: [],
      editDescs:{ //
          roleName:'',
          roleDesc:''
      },
      addDesc:{  //
          roleName:'',
          roleDesc:''
      },
      editDescdialog:false,//
      addRoleDesc:false,//
      showRight:false,//
      rightList:[],//
    //   
    treeProps:{
        label:'authName',
        children:'children'
    },
    //
    defkeys:[],
    roleId:''//ID
    }
  },
  watch: {},
  computed: {},
  methods: {
      //
    async getRoleList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.roleList = res.data
      console.log(this.roleList)
    },
    //
    addDescClose(){
     this.$refs.addDescRef.resetFields()
    },
    //
    addroleDesc(){
        this.$refs.addDescRef.validate(async valid => {
            if(!valid) return 
            //
           const { data:res } = await this.$http.post('roles',this.addDesc)
           if(res.meta.status !== 201){
              this.$message.error('')
           }
            this.$message.success('')
            this.addRoleDesc = false
            //
            this.getRoleList()
        })
    },
    //
    async editDesclog(id){
        this.editDescdialog = true
      const { data:res } = await this.$http.get('roles/' + id)
      if(res.meta.status !== 200){
          return this.$message.error('')
      }
      this.editDescs = res.data
    },
      //
    editdescClose(){
        this.$refs.editDescRef.resetFields()
    },
    //
     editDescInfo(){
        this.$refs.editDescRef.validate( async valid =>{
            console.log(valid)
            if(!valid) return 
            //
           const { data:res } = await this.$http.put('roles/'+this.editDescs.roleId,{
               roleName:this.editDescs.roleName,
               roleDesc:this.editDescs.roleDesc
           })
           if(res.meta.status !== 200){
              return this.$message.error('')
           }
            //
            this.editDescdialog = false
            this.$message.success('')
             this.getRoleList()//
        })
    },
      //ID
    async editRemove(id){
        //
      const Reault = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        // cancel
          if(Reault !== 'confirm'){
              return this.$message.info('')
          }
          const {data:res } = await this.$http.delete('roles/' + id)
          if(res.meta.status !== 200){
               return this.$message.error('')
          }
          this.$message.success('')
          this.getRoleList()
    },
    //Id
   async removeRightById(role,rightId){
        const confirmResult = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if(confirmResult !== 'confirm'){
             return this.$message.info('')
        }
        // console.log()
       const { data:res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if(res.meta.status !== 200){
               return this.$message.error('')
          }
          this.$message.success('')
        //   this.getRoleList()
          role.children = res.data
    },
    //
    async showDesc(role){
        this.roleId = role.id
        const { data:res } = await this.$http.get('rights/tree')
        if(res.meta.status !== 200){
            return this.$message.error('')
        }
        //
        this.rightList = res.data
        console.log(this.rightList)
        //
        this.getLeafKeys(role,this.defkeys)
        //
        this.showRight = true

    },
    //iddefkeys
    getLeafKeys(node,arr){
        if(!node.children){
            return arr.push(node.id)
        }
        node.children.forEach(item =>this.getLeafKeys(item,arr))
    },
    // 
    setRightDialogClose(){
        this.defkeys = []
    },
    //
   async allotRights(){
        const keys = [
            ...this.$refs.treeRef.getCheckedKeys(),
             ...this.$refs.treeRef.getHalfCheckedKeys(),
        ]
        const idStr = keys.join(',')
       const { data:res } = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
       if(res.meta.status !== 200){
             return this.$message.error('')
       }
         this.$message.success('')
         this.getRoleList()
         this.showRight = false
    }

  },
  created() {
    this.getRoleList()
  },
  mounted() {},
}
</script>
<style lang="less" scoped>
.el-tag{
    margin:7px;
}
.bdtop{
    border-top:1px solid #eee;
}
.bdbottom{
    border-bottom:1px solid #eee;
}
.vcenter{
    display:flex;
    align-items:center;
}
</style>